.modal-common{
    .ant-modal-body{
        padding: 40px 24px;
        .modal-inner-connent{
            
        }
        .ant-form-item{
            &:last-child{
                margin: 0;
            }
            .ant-form-item-label {
                label{
                    height: 44px;
                }
            }
            .ant-radio-group{
                line-height: 44px;
            }
            .ant-input{                
                padding: 10px 20px;
            }
            .ant-select-selector{
                height: 44px;
                .ant-select-selection-item{
                    line-height: 42px;
                }                
            }
            .ant-select-multiple{
                .ant-select-selection-item{
                    line-height: normal;
                }
            }
            .ant-input-number{
                width: 100%;
                height: 44px;
                line-height: 42px;
            }
        }        
    }
}