.page-body{
    width: 100%;
    background: #fff;
    border-radius: 5px;
    .panel{
        padding: 40px;
        .panel-title{
            color: #000;
            font-size: 18px;
            margin-bottom: 12px;
        }
        .panel-body{
            .action{
                margin-bottom: 24px;
                overflow: hidden;
            }
        }
    }
    .td-img{        
        width: 80px;
    }    
    .ant-btn{
        color: #fff;
        font-size: 14px;
        border: 0;
        border-radius: 3px;

        &:hover,&:focus{
            color: #fff;
        }
        &.btn-add{            
            background: linear-gradient(to right, #84d9d2, #07cdae);
        }
        &.btn-edit{
            background: linear-gradient(89deg, #5e7188, #3e4b5b);
        }
        &.btn-delete{
            background: linear-gradient(to right, #ffbf96, #fe7096);
        }
    }
    .ant-btn[disabled]{
        color: rgba(0,0,0,.25);
        background: #f5f5f5;
        border: 1px solid #d9d9d9;
    }
}
.searchName{
    text-align:right;
    margin-right: 45px;
    margin-bottom: 21px;
}
.ant-table-thead {
    tr{
        th{
            padding: 10px;
        }
    }
    
}