.page-sider{
    background: #fff;
    :global .ant-menu{
        height: 100%;
        border-right: 0;
        &.ant-menu-inline-collapsed{
            a{
                display: inline-block;
                max-width: 0;
                opacity: 0;
            }
        }
        .ant-menu-item{
            height: 56px;
            line-height: 56px;
            margin: 0;
        }
    }  
    
}
